#home-container {
    height: 100vh;
    position: relative;
    overflow: hidden;
    background: #000
}
.mictlan-home {
    width: 100%;
    background: #000;
    color: #dfdad3;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.mc-home-bottom {
    position: absolute;
    z-index: 10;
    color: #a5a5a5;
    font-weight: 600;
    font-size: .16rem;
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    bottom: 0.2rem;
    display: flex;
    grid-gap: 0.3rem;
    gap: 0.3rem;
    align-items: baseline;
    left: 0.5rem;
}
.mc-home-bottom-right {
    position: fixed;
    z-index: 10;
    color: #a5a5a5;
    font-weight: 600;
    font-size: .16rem;
    margin-left: var(--main-margin);
    -webkit-user-select: none;
    -ms-user-select: none;
        user-select: none;
    bottom: 0.05rem;
    right: 0.5rem;
    display: flex;
    grid-gap: 0.5rem;
    gap: 0.5rem;
    align-items: center;
}
.kickstarter p {
    font-family: 'Avenir';
    font-size: .14rem;
}
.kickstarter img {
    width: 1.6rem;
}
.steam {
    display: flex;
    justify-content: space-between;
    grid-gap: 0 10px ;
    gap: 0 10px ;
    align-items: center;
}
.steam img {
    width: 0.25rem
}
.steam button {
    background-color: white;
    padding: 0.08rem 0.15rem;
    border: none;
    border-radius: 0.1rem;
    font-family: "SF-Compact-Bold";
    font-size: 0.15rem;
}
.steam button:hover {
    transform: scale(0.9);
    background-color: black;
    color: white;
}
.meta {
    position: relative;
    bottom:10px;
}
.meta h4 {
    margin: 0;
    font-family: 'Lequire';
    font-size: 0.25rem;
}
.meta p {
    font-family: "SF-Compact-Rounded-Light";
    margin-top: -21px;
